import Entity from './Entity';


class Cell {
  constructor(x, y, entityId = 0, val) {
    this.entity = new Entity(entityId, val);
    this.x = x;
    this.y = y;
  }

  //  static  localStorage
  // 
  static setEntityById(cell, entityId) {
    cell.entity = new Entity(entityId);
  }
}



export default Cell;


